<template>
  <!-- <button @click="$store.commit('guan')"> x</button> -->
  <div id=" login" class="content-page" v-show="$store.state.isdenglushow">
    <div style="
        max-width: 450px; 
        margin: 0 auto;
        background: white;
        border-radius: 16px;
        box-shadow: var(--card-shadow);
        overflow: hidden;
        width: 395px;
      ">
      <div style="background: var(--primary-color); color: white; text-align: center; padding: 30px">
        <h2 style="font-size: 28px; margin-bottom: 10px">欢迎回来</h2>
        <p>志愿社区管家平台</p>
      </div>
      <div style="padding: 30px">
        <!-- 电子邮箱 -->
        <div class="form-group">
          <label for="login-username">电子邮箱</label>
          <input type="text" id="login-username" class="form-control" placeholder="请输入邮箱" name="email" v-model="email"
            @input="jiancename" />
          <span class="yonghumingcuowu">{{ yonghuemail }}</span>
        </div>
        <!-- 密码 -->
        <div class="form-group">
          <label for="login-password">密码</label>
          <input type="password" id="login-password" class="form-control" placeholder="请输入密码" name="Password"
            v-model="password" @input="jiancepassword" />
          <span class="yonghumingcuowu">{{ yonghupassword }}</span>
        </div>
        <!-- 协议 -->
        <div style="display: flex;justify-content: space-between;align-items: center; margin-bottom: 20px;">
          <div>
            <input type="checkbox" id="remember" style="cursor: pointer;" v-model="tongyi" @click="iftongyi" />
            <label style="margin-left: 5px ;color: var(--primary-color);cursor: pointer;" @click="xieyi">同意协议</label>
          </div>
          <a href="#" style="color: var(--primary-color); text-decoration: none" @click="wnagijpassdorw">忘记密码?</a>
        </div>
        <!-- 登录按钮 -->
        <div style="display: flex;justify-content: center;align-items: center; margin-top: 20px;">
          <button :disabled="isDisabled" class="btn btn-primary btn-block" id="loginBtn" type="submit" @click="denglu">
            {{ annuvalue }}
          </button>
        </div>
        <!-- 立即注册 -->
        <div style="text-align: center; margin-top: 25px; padding-top: 20px;border-top: 1px solid var(--border-color);">
          <p>
            还没有账号?
            <a id="goToRegister" style=" color: var(--primary-color); text-decoration: none; font-weight: 500"
              @click="lijizhuce">立即注册</a>
          </p>
        </div>
      </div>

    </div>
  </div>
</template>
<script>
// import api from "@/store/axiosqingqiu";
// import { mapState } from "vuex";
import loadXMLDoc from '@/store/AjaxRequest.js';
import BrowserID from '@/store/BrowserID';

export default {
  data() {
    return {
      //name
      email: "",
      yonghuemail: "",

      //password
      password: "",
      yonghupassword: "",

      // 眼睛
      passwordortext: true,

      //记住我
      ifjizhume: false,

      //同意协议
      tongyi: false,

      //用户信息
      userarr: "",

      //登录错误
      dengluname: '',
      denglupassword: "",

      //按钮
      isDisabled: false,
      annuvalue: "登录",

      // 电脑ID
      DeviceID: "",

    };
  },
  // created() { },

  methods: {
    //登录
    denglu() {
      if ((this.email).trim() == "" || (this.password).trim() == "") {
        this.$store.commit("tanchaung", "请输入用户名和密码")
        this.$store.commit("tanchuangshow", true)
        return
      }

      this.isDisabled = true;
      this.annuvalue = "登录中...";

      if (this.tongyi) {
        let tokenFetchFailed = false;
        //token
        loadXMLDoc({
          reqBody: { 'email': this.email, 'password': this.password, 'deviceid': this.DeviceID },
          // reqBody: { 'email': "hm2897135735@qq.com", 'password': "88888888", 'deviceid': this.DeviceID },
          reqUrl: 'http://8.148.31.153:3030/login',
          async: false,
          callback: (error, resData) => {
            try {
              const data = JSON.parse(resData)
              if (!error) {
                // 拿到token
                if (data.success) {
                  localStorage.setItem("token", null)
                  localStorage.setItem('token', data.resData.token);
                } else {
                  // 没拿到token
                  tokenFetchFailed = true;
                  this.$store.commit("tanchaung", data.message.msg)
                  this.$store.commit("tanchuangshow", true)
                  this.annuvalue = "登录";
                  this.isDisabled = false;
                  this.tongyi = !this.tongyi
                }
              }
            } catch (error) {
              console.log(error)
            }
          }
        })
        if (tokenFetchFailed) {
          return;
        }
        const value = localStorage.getItem("token")
        if (value == "null" || value == "undefined" || value == '') {
          this.$store.commit("tanchaung", "账号或密码错误")
          this.$store.commit("tanchuangshow", true)
          this.annuvalue = "登录";
          this.isDisabled = false;
          return
        }

        //登录
        loadXMLDoc({
          reqBody: {
            'token': localStorage.getItem("token"), 'deviceid': localStorage.getItem("equipment")
          },
          reqUrl: 'http://8.148.31.153:3030/getInfos',
          async: false,
          callback: (error, resData) => {
            try {
              const data = JSON.parse(resData)
              if (!error) {
                if (data.success) {
                  //本地存储
                  localStorage.setItem("denglutoken", true)
                  //改文字
                  this.$store.commit("dlcg", "退出登录")
                  //登录成功的用户信息
                  localStorage.setItem("resData", resData)
                  this.$store.commit("yonghuxx", resData)
                  // const times1 = setTimeout(() => { this.$router.push({ name: "FunctiontWorkbench" }), this.$store.commit("ifisgongzuotaishow", true) }, 2000)
                  this.$router.push({ name: "FunctiontWorkbench" })
                  this.$store.commit("ifisgongzuotaishow", true)
                  this.tokenOk = false
                  this.isDisabled = false
                } else {
                  this.$store.commit("tanchaung", data.message.msg == "字段缺失" ? "请输入用户名或密码" : data.message.msg)
                  this.$store.commit("tanchuangshow", true)
                  this.annuvalue = "登录"
                  this.isDisabled = false
                }
              }
            } catch (error) {
              console.log(error)
            }
          }
        })
      } else {
        // 不同意  
        this.annuvalue = "登录"
        this.$store.commit("tanchaung", "同意协议方可登录")
        this.$store.commit("tanchuangshow", true)
        this.isDisabled = false
      }
      // 活动类型
      loadXMLDoc({
        reqBody: { 'token': localStorage.getItem("token"), 'deviceid': localStorage.getItem("equipment") },
        reqUrl: "http://8.148.31.153:3030/getConfig",
        async: false,
        callback: (error, resdata) => {
          try {
            if (!error) {
              const data = JSON.parse(resdata)
              this.ActivitySelectedArr = data.resData
              localStorage.setItem("getConfig", resdata)
              this.$store.commit("activeTypeOk", data)
            }
          } catch (erroe) {
            console.log(erroe)
          }
        }
      })
    },

    //同意协议
    iftongyi() {
      this.tongyi = !this.tongyi
      BrowserID.getCombinedId(response => {
        if (response.success) {
          this.DeviceID = response.combinedId;
          localStorage.setItem("equipment", this.DeviceID)
        } else {
          this.error = response.error;
        }
      });
    },

    //输入用户名
    jiancename() {
      //name
      if (!/^[a-zA-Z0-9\u4e00-\u9fa5._-]+@[a-zA-Z0-9.-]+\.[a-zA-Z]{2,}$/.test(this.email)) {
        this.yonghuemail = "电子邮件格式不正确"
        return;
      }
      if (this.email == "") {
        (this.yonghuemail = "电子邮件不能为空")
        return;
      }
      if (/\s/.test(this.email)) {
        (this.yonghuemail = "电子邮件不能有空格")
        return;
      }
      if (this.email.length > 64) {
        this.yonghuemail = "电子邮件不应超过64个字符";
        return;
      }
      this.yonghuemail = ""
    },
    //输入密码
    jiancepassword() {

      if (/^.{8,16}$/.test(this.password)) {
        this.yonghupassword = ""
      } else if (this.password === "") {
        (this.yonghupassword = "密码不能为空")
      } else {
        (this.yonghupassword = "密码应为8~16个字符");
      }
    },
    //记住我
    jizhume() {
      this.ifjizhume = !this.ifjizhume
      if (this.ifjizhume) {
        this.$store.commit("ifjizhuwo")
      }
    },
    //协议
    xieyi() {
      this.$router.push({ name: "UserAgreement" })
    },
    //忘记密码
    wnagijpassdorw() {
      this.$router.push({ name: "UserForgetPassword" })
    },
    //注册
    lijizhuce() {
      this.$router.push({ name: "UserRegistration" });
    },
  },
};
</script>
<style scoped>
:root {
  --primary-color: #3498db;
  --primarys-hover: #358ac3;
  --border-color: #e0e0e0;
  --text-color: #333;
  --background-light: #f8f9fa;
  --card-shadow: 0 4px 20px rgba(0, 0, 0, 0.08);
  --transition: all 0.3s ease;
}

.yonghumingcuowu {
  display: block;
  user-select: none;
  pointer-events: none;
  caret-color: transparent;
  color: red;
  font-size: 13px;
  margin-top: 6px;
  display: block;
  height: 14px;
}

.content-page {
  width: 100%;
  margin: 0 auto;
}

.register-container {
  max-width: 800px;
  margin: 0 auto;
  background: white;
  border-radius: 16px;
  box-shadow: var(--card-shadow);
  overflow: hidden;
  width: 100%;
}

.page-header {
  background: var(--primary-color);
  color: white;
  text-align: center;
  padding: 30px;
}

.page-header h2 {
  font-size: 28px;
  margin-bottom: 10px;
  font-weight: 600;
}

.page-header p {
  font-size: 16px;
  opacity: 0.9;
}

.form-content {
  padding: 30px;
}

.form-row {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  margin-bottom: 20px;
}

.form-col {
  flex: 1;
  min-width: 250px;
}

.form-group {
  margin-bottom: 5px;
  position: relative;
}

.form-group label {
  display: block;
  margin-bottom: 8px;
  font-weight: 500;
  font-size: 15px;
  color: #555;
}

.form-control {
  width: 100%;
  padding: 12px 15px;
  border: 1px solid var(--border-color);
  border-radius: 8px;
  font-size: 15px;
  transition: var(--transition);
}

.form-control:focus {
  outline: none;
  border-color: var(--primary-color);
  box-shadow: 0 0 0 3px rgba(52, 152, 219, 0.2);
}

.verification-group {
  display: flex;
  gap: 10px;
}

.verification-group .form-control {
  flex: 1;
}

.verification-code-btn {
  background-color: #f8f9fa;
  border: 1px solid var(--border-color);
  border-radius: 8px;
  padding: 0 15px;
  cursor: pointer;
  color: var(--primary-color);
  font-weight: 500;
  white-space: nowrap;
  transition: var(--transition);
}

.verification-code-btn:hover {
  background-color: #e3f2fd;
}

.agreement-group {
  margin: 13px 0;
  display: flex;
  align-items: flex-start;
}

.agreement-group input {
  margin-top: 3px;
  margin-right: 8px;
}

.agreement-group a {
  color: var(--primary-color);
  text-decoration: none;
}

.agreement-group a:hover {
  text-decoration: underline;
}

.btn {
  display: block;
  width: 100%;
  padding: 13px 20px;
  background-color: var(--primary-color);
  color: white;
  font-size: 16px;
  font-weight: 500;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  transition: var(--transition);
}

.btn:hover {
  background-color: rgb(30, 136, 229, 0.7);
  transform: translateY(-2px);
}

.btn:active {
  transform: translateY(0);
}

.login-link {
  text-align: center;
  margin-top: 25px;
  padding-top: 20px;
  border-top: 1px solid var(--border-color);
  font-size: 15px;
}

.login-link a {
  color: var(--primary-color);
  text-decoration: none;
  font-weight: 500;
  margin-left: 5px;
}

#goToRegister:hover {
  cursor: pointer;
}
</style>