// BrowserID.js
import FingerprintJS from "@fingerprintjs/fingerprintjs";

class BrowserID {
  constructor() {
    this.fpPromise = null;
    this.init();
  }

  // 初始化 FingerprintJS
  init() {
    try {
      this.fpPromise = FingerprintJS.load();
      return true;
    } catch (error) {
      console.error("初始化指纹库失败:", error);
      return false;
    }
  }

  // 获取组合ID（设备ID + 时间组合ID）
  getCombinedId(successCallback, errorCallback) {
    // 确保已初始化
    if (!this.fpPromise) {
      if (!this.init()) {
        errorCallback("无法初始化指纹库");
        return;
      }
    }

    // 获取设备ID
    this.fpPromise
      .then((fp) => fp.get())
      .then((result) => {
        const deviceId = result.visitorId;

        // 生成时间组合ID
        const timestamp = new Date().getTime();
        const dateStr = new Date(timestamp).toISOString().replace(/[-:T]/g, "").slice(0, 14);
        const combinedId = `${deviceId}_T${dateStr}`;

        // 返回最终结果
        successCallback({
          deviceId,
          combinedId,
          timestamp,
        });
      })
      .catch((error) => {
        errorCallback("获取组合ID失败: " + error.message);
      });
  }
}

// 创建并导出 BrowserID 实例
const browserID = new BrowserID();

// 导出单一公共API
export default {
  /**
   * 获取组合ID（包含设备ID和时间组合ID）
   * @param {function} callback - 回调函数，接收 { success: boolean, deviceId?: string, combinedId?: string, timestamp?: number, error?: string }
   */
  getCombinedId: function (callback) {
    browserID.getCombinedId(
      (result) =>
        callback({
          success: true,
          deviceId: result.deviceId,
          combinedId: result.combinedId,
          timestamp: result.timestamp,
        }),
      (error) =>
        callback({
          success: false,
          error,
        })
    );
  },
};
